.container {
  width: 1400px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
}

.carList {
  display: flex;
  flex-wrap: wrap;
  gap: 29px;
  padding: 0;
  justify-content: center;
}

.cartItem {
  width: 274px;
  text-decoration: none;
  list-style-type: none;
}

.button {
  color: #3470ff;
  display: block;
  font-family: Manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px; /* 150% */
  text-decoration: none;
  margin-top: 50px;
  border: none;
  background: #fff;
  margin-left: auto;
  margin-right: auto;
}
